import { serverApi } from './config';

// 
export const fetchRoomList = () => {
  return serverApi({
    method: 'get',
    url: '/rooms'
  });
};

// 
export const fetchRoomDetail = (roomId) => {
  return serverApi({
    method: 'get',
    url: `/room/${roomId}`
  });
};

// 
export const reserveRoom = (roomId, data) => {
  return serverApi({
    method: 'post',
    url: `/room/${roomId}`,
    data
  });
};

// 
export const unreserveAllRooms = () => {
  return serverApi({
    method: 'delete',
    url: `/rooms`
  });
};
